@use '../helpers/variables';
@use '../mixins/box';
@use '../mixins/common';

.header {
  position: fixed;
  @include common.set-position($t: 0, $l: 0, $r: 0);
  background-color: variables.$primary;
  padding: 12px;
  z-index: 10;
 
  &_icon {
    @include common.initialize-appearance();
    @include box.fixed-box(24, 24);
    background: variables.$transparent;
    padding: 0;
    position: relative;
    z-index: 2;

    &_interval {
      margin-left: 13px;
    }

    img {
      @include box.fixed-box(100%, 100%);
    }
  }

  &_text {
    @include common.set-position(
      $t: 12px, 
      $l: 25%, 
      $r: 25%, 
      $b: 12px
    );
    color: variables.$white;
    position: absolute;
    text-align: center;
    line-height: 24px;
    font: {
      size: 17px;
      weight: 700;
    }
  }

  @include common.clear-fix();
}

