@use '../helpers/variables';

@mixin font-face($font-name, $weight, $type) {
  $font-file-name: $font-name + '-' + $weight;

  @font-face {
    font : {
      family: $font-name;
      style: normal;
      weight: map-get(variables.$font-weight, $weight);
    }
    src: url('/assets/fonts/' + $font-file-name + '.woff2') format('woff2'),
         url('/assets/fonts/' + $font-file-name + '.woff') format('woff'),
         url('/assets/fonts/' + $font-file-name + '.' + $type) format(if($type == 'ttf', 'truetype', 'opentype'));
  }
}