
@mixin clear-fix() {
  &::after {
    display: block;
    clear: both;
    content: '';
  }
}

@mixin set-position(
  $t: null,
  $l: null,
  $r: null,
  $b: null
) {
  top: $t;
  bottom: $b;
  left: $l;
  right: $r;
}

@mixin initialize-appearance() {
  opacity: 1;
  -webkit-text-fill-color: inherit;
  appearance: none;
  -moz-appearance: none;
  -webkit-appearance: none;
  border: 0;
  outline: none;
}