@use '../helpers/variables';
@use '../mixins/box';
@use '../mixins/common';

.room_container {
  @include box.fixed-box(100%, 100%);
  background-color: variables.$pale-grey;
}

.message_container {
  padding-top: 44px;
  margin: {
    left: 16px;
    right: 16px;
  }
  height: calc(100% - 130px);
  height: -webkit-calc(100% - 130px);
  overflow: auto;
}

.message_row {
  margin-top: 10px;
  display: flex;
  display: -webkit-flex;
  column-gap: 4px;
  align-items: flex-end;

  &.me {
    flex-direction: row-reverse;
  }

  &.you {
    flex-direction: row;
  }

  .message_column {
    &.text {
      max-width: 75%;
    }

    &.time {
      opacity: .4;
      color: variables.$charcoal-grey-two;
      font-size: 12px;
    }
  }
}

.message_box {
  padding: 12px;
  border-radius: 12px;
  box-shadow: 0 2px 4px variables.$black-transparent;
  font-size: 14px;

  &.me {
    color: variables.$pale-grey;
    background-color: variables.$primary;   
  }

  &.you {
    color: variables.$charcoal-grey-two;
    background-color: variables.$white;
  }
}

.image_box {
  border-radius: 12px;
  overflow: hidden;
  width: 60%;

  .box_square {
    height: 0;
    padding-bottom: 100%;
    position: relative;

    img {
      position: absolute;
      @include common.set-position($t: 0, $l: 0);
      width: 100%;
    }
  }

  
}

.date_divider {
  position: relative;
  border-top: 1px solid variables.$pale-lilac;
  margin-top: 17px;
  text-align: center;

  .date_text {  
    position: relative;
    display: inline-block;
    top: -9px;
    color: variables.$charcoal-grey-two-alpha;
    background-color: variables.$pale-grey;
    font-size: 12px;
    padding: {
      left: 15px;
      right: 15px;
    }
  }
}

.message_input {
  padding: {
    left: 16px;
    right: 16px;
  }
  display: flex;
  display: -webkit-flex;
  column-gap: 12px;
  margin-top: 16px;

  .message_input_textarea {
    max-width: calc(100% - 62px);
    flex-grow: 2;
    flex-basis: auto;

    input {
      @include common.initialize-appearance();
      width: 100%;
      box-sizing: border-box;
      padding: 16px;
      background-color: variables.$white;
      font-size: 14px;
      letter-spacing: -.12px;
      box-shadow: 0 2px 4px variables.$black-transparent;
      border-radius: 25px;

      &::placeholder {
        color: variables.$battleship-grey;
      }
    }
  }

  .message_input_buttonarea {
    width: 50px;
    flex-basis: 50px;

    button {
      @include box.fixed-box(50, 50);
      @include common.initialize-appearance();
      border-radius: 50%;
      background-color: variables.$primary; 

      img {
        @include box.fixed-box(26, 18);
      }
    }
  }
}

.image_storage {
  position: absolute;
  @include common.set-position($t: 50px, $l: 0, $r: 0);
  background-color: variables.$primary;
  padding: 11px 0;
  overflow: {
    x: auto;
    y: hidden;
  };
  -webkit-overflow-scrolling: touch;

  .image_column {
    display: table-cell;
    @include box.fixed-box(46, 46);
    padding: 0 5px;

    &:first-child {
      padding-left: 16px;
    }
    
    &:last-child {
      padding-right: 16px;
    }

    img {
      @include box.fixed-box(46, 46);
      border-radius: 5px;
    }
  }
}