@use '../helpers/variables';
@use '../mixins/box';

.chat_list_container {
  padding: 54px 0;
}

.chat_listbox {
  display: flex;
  display: -webkit-flex;
  column-gap: 15px;
  padding: 9px 16px;
  text-decoration: none;
  align-items: center;

  .chat_listbox_profile {
    max-width: 56px;
    flex-basis: 56px;

    img {
      @include box.fixed-box(56, 56);
      border-radius: 50%;      
      display: block;
    }
  }

  .chat_listbox_text {
    flex-grow: 2;
    max-width: calc(100% - 115px);
    flex-basis: auto;

    h4 {
      font: {
        size: 16px;
        weight: 700;
      }
      color: variables.$charcoal-grey;
      margin-bottom: 6px;
      line-height: 19px;
      letter-spacing: -.2px;
    }

    p {
      font: {
        size: 13px;
      }
      color: variables.$cool-grey;
    }
  }

  .chat_listbox_info {
    text-align: right;
    max-width: 29px;
    flex-basis: 29px;

    .time {
      display: block;
      opacity: .4;
      font-size: 11px;
      color: variables.$charcoal-grey-two;
      margin-bottom: 5px;
    }
  }
}

.badge {
  display: inline-block;
  min-width: 18px;
  height:18px;
  line-height: 18px;
  background-color: variables.$primary;
  font-size: 10px;
  color: variables.$white;
  letter-spacing: -.08px;
  border-radius: 9px;
  text-align: center;
  box-sizing: border-box;
  padding: {
    left: 5px;
    right: 5px;
  }

  &_none {
    opacity: 0;
  }
}